//vuex状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

// 全局注册
Vue.use(Vuex);
const store = new Vuex.Store({
    //数据放在state中便于共享

    // this.$store.state.属性名 A
    // 借助mapState ... B
    state:{
        topics:[
            {name:'all',desc:'全部',selected:true},
            {name:'good',desc:'精华',selected:false},
            {name:'share',desc:'分享',selected:false},
            {name:'ask',desc:'问答',selected:false},
            {name:'job',desc:'招聘',selected:false},
            {name:'dev',desc:'客户端测试',selected:false},
          ],
          
          // 用户未登录显示登陆
            unloginedMenus:{
            login:'登录',
            // menu2:'注册',
            // menu3:'取消'
            },
          // 用户已登录显示
          loginedMenus:{
            create:'发表新话题',
            msg:'消息通知',
            center:'个人中心',
            logout:'退出',
          },
            
          isLogined:false,
          isShowMoreMenus:false,
          loginname:'',
          // accesstoken
          accesstoken:'',
          // 状态数据修改只能借助于mutation
          author_id:'',
    },
    getters:{
        moreMenus:function(state){
            // 函数调用加入state
            if(state.isLogined){
            // this替换为state
            // 登陆状态
            return state.loginedMenus;
            } 
            return state.unloginedMenus;
            // return this.unloginedMenus;
          },
          getTopicDescByTab:function(state){
            return function(tab){
              for(var i=0;i<state.topics.length;i++){
                if(state.topics[i].name==tab){
                  return state.topics[i].desc;
                }
              }
            }
          },

    },
    
    //同步修改
    mutations,
    //异步修改
    actions
})
// 对外输出
export default store;


